body {
  width: 1080px;
  background: black;
  font-size: 2em;
  border: 1px dashed #ffffff;
  color: white;
}
body,
button,
input,
select,
textarea {
  color: #fff;
  font-family: Exo Web, sans-serif;
  font-size: 1rem;
  line-height: 1.5;
}
a,
a:visited {
  color: #0c0;
}

.page {
  margin: 0 auto;
  max-width: 1080px;
  padding: 2.5%;
  width: 100%;
}

.site-content {
  margin: 4.5rem 175px 1.5rem;
  position: relative;
}

.site-content::before {
  content: '\f10d';
  left: -175px;
}

.site-content::after {
  content: '\f10e';
  right: -175px;
}

.site-content::after,
.site-content::before {
  color: #0c0;
  font-family: FontAwesome;
  font-size: 125px;
  line-height: 125px;
  position: absolute;
  top: 0;
}

