.my-content {
  margin: 0 auto;
  max-width: 1080px;
  padding: 2.5%;
  width: 100%;
  p {
    font-size: 2.5rem;
  }
}

.title h2 {
  display: inline;
  font-size: 1.25rem;
  font-weight: 400;
  // margin-bottom: 0px;
}

button {
  background-color: #222;
  border: 2px solid #0c0;
  border-radius: 3px;
  color: #0c0;
  font-size: 1.25rem;
  font-weight: 700;
  line-height: 1;
  padding: 0.75rem 2.25rem;
  cursor: pointer;
  margin-left: 40%;
}
